import React, { Component } from 'react';
import {defaultUserPic,eosIcon,inviteIcon,shbIcon,stockIcon,rnbIcon,bookIcon,ruleIcon,wxIcon,ariIcon,languageIcon} from '../../public';
import "./menu.css";
import List from './list';
import Eos from '../eosRecarge/eosRecarge';
import EOS from 'eosjs';
const TABLE_RED_PAPER="redenvelope";//红包表
const TABLE_RED_STATE="gameruntime";//红包状态表
const TABLE_GET_RED_PAPER="grabre";//抢红包表
const TABLE_GAME_CONFIG="gameconf";//游戏配置表
const TABLE_PLAY_BALANCE="playbalance";//游戏玩家表
let contract_name="gre12345game";
let account_name="gre53palyer3";
let account_name2="gre53palyer2";
let account_name3="gre53palyer3";
const CONFIG={
	httpEndpoint:'http://www.super.lighting:8888',
	code_as_wasm: true,
	chain_id:'cf057bbfb72640471fd910bcb67639c22df9f92470936cddc1ade0e2f2e7dc4f',
	
//	keyProvider:["PW5KAHS48gxwJeGjd2xh8RMqDpbPSH2SeLuYBNBdpoM2HyL6pt6W2"],
	keyProvider:['5KQwrPbwdL6PhXujxW37FSSQZ1JiwsST4cqQzDeyXtP79zkvFD3','5Jmsawgsp1tQ3GD6JyGCwy1dcvqKZgX6ugMVMdjirx85iv5VyPR']
}
class Menu extends Component{
	constructor(props) {
    super(props);
    this.state = {
    	a:1,
    	menuCallBack:props.callBack,
    	account_info:{}
    };
    
  }
	eosClick(){
		this.state.menuCallBack('openEos');
	}
	inviteClick(){
		this.state.menuCallBack('openInvite');
	}
	changeLang(){
		let lang=localStorage.getItem("lang");
		lang=="EN"?lang="CN":lang="EN";
		localStorage.setItem("lang",lang);
	}
	hClick(fun){
		console.log(fun);
	}
	goLogin(){//登录
		this.state.menuCallBack('openLogin');
		console.log('login-----')
	}
	loadAccount(row,account_name){
		let eos=EOS(CONFIG);
		let _this=this;
		eos.getTableRows({"scope":contract_name,"code":contract_name,"lower_bound":row*10,"table":TABLE_PLAY_BALANCE,"json":true}).then(res=>{
			let rows=res.rows;
			for(let i=0;i<rows.length;i++){
				let obj=rows[i];
				if(obj.player==account_name){
					_this.setState({account_info:obj});
					return ''
				}
			}
			if(res.more){
				_this.loadAccount(row+1,account_name);
			}
		})
	}
	componentWillMount(){
		let account_name=localStorage.getItem('account_name');
		
		this.loadAccount(0,account_name);
	
	}
	render(){
		let lang=localStorage.getItem("lang");
		lang=lang?lang:"CN";
		let user_name=localStorage.getItem("account_name");
		let numBalance=this.state.account_info.balance;
		numBalance=parseFloat(numBalance);
		return (
			<div>
				<div className="menu-wrap">
					<div className="menu-login flex-start" onClick={this.goLogin.bind(this)}>
						<div className="img-box user-pic">
							<img src={defaultUserPic}/>
						</div>
						<span className="user-name">{user_name?user_name:"尚未登录"}</span>
					</div>
					<List callBack={this.eosClick.bind(this)} key='1' theme="1" num={numBalance} title="eos" src={eosIcon}></List>
					<List callBack={this.hClick.bind(this)} key='2' theme="1" num={0} title="shb" src={shbIcon}></List>
					<List callBack={this.inviteClick.bind(this)} key='3' theme="" title={lang=="CN"?"邀请好友":"Invite Friends"} src={inviteIcon}></List>
					<List callBack={this.hClick.bind(this)} key='4' theme="" title={lang=="CN"?"质押分红":"Participation in profit"} src={rnbIcon}></List>
					<List callBack={this.hClick.bind(this)} key='5' theme="" title={lang=="CN"?"白皮书":"Whitebook"} src={bookIcon}></List>
					<List callBack={this.hClick.bind(this)} key='6' theme="" title={lang=="CN"?"股权置换":"Equity Swap"} src={stockIcon}></List>
					<List callBack={this.changeLang.bind(this)} key='7' theme="" title={lang=="CN"?"简体中文":"EN"} src={languageIcon}></List>
					<List callBack={this.hClick.bind(this)} key='8' theme="" title={lang=="CN"?"玩法说明":"Rule"} src={ruleIcon}></List>
					<List callBack={this.hClick.bind(this)} key='9' theme="" title={lang=="CN"?"添加客服":"Wechat Service"} src={wxIcon}></List>
					<List callBack={this.hClick.bind(this)} key='10' theme="" title={lang=="CN"?"加入电报群":"Telegram Group"} src={ariIcon}></List>
			
				</div>
			</div>
			
		)
	}
}
export default Menu;